.how-go {
    display: flex;
    flex-direction: column;
    margin-bottom: 95px;

    background-color: #ffffff;
}

.how-go__title {
    font-size: 28px;
    line-height: 30px;
    text-align: center;
    font-weight: 600;
    color: #000000;
    text-transform: uppercase;

    margin: 0;
    padding: 0;
    padding-top: 75px;
    padding-bottom: 30px;
}

.how-go-list {
    list-style: none;
    margin: 0;
    padding: 0 27px;
}

.how-go-list__item {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 300;
    color: #000000;
    
    padding: 20px 0 0 30px;
    position: relative;

    &::before {
        content: "";

        position: absolute;
        
        border-top: 10px solid transparent;
        border-bottom: 10px solid transparent;
        border-left: 10px solid #222222;
        
        opacity: 0.5;

        left: 0;
        top: 50%;
    }
}
